<template>
	<div
		:style='{ "width": "100%", "padding": "30px 7% 40px", "margin": "0px auto", "position": "relative", "background": "#fff" }'>
		<el-form
			:style='{ "border": "1px solid #eee", "width": "100%", "padding": "30px", "position": "relative", "background": "#fcfcfc" }'
			class="add-update-preview" ref="ruleForm" :model="ruleForm" :rules="rules" label-width="180px">
			<el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
				prop="paotuiyuanzhanghao">
				<el-input v-model="ruleForm.paotuiyuanzhanghao" placeholder="" clearable
					:disabled="false || ro.paotuiyuanzhanghao"></el-input>
			</el-form-item>
			<el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label="" prop="mima">
				<el-input v-model="ruleForm.mima" placeholder="" clearable :disabled="false || ro.mima"></el-input>
			</el-form-item>
			<el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
				prop="paotuiyuanxingming">
				<el-input v-model="ruleForm.paotuiyuanxingming" placeholder="" clearable
					:disabled="false || ro.paotuiyuanxingming"></el-input>
			</el-form-item>
			<el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label="" prop="xingbie">
				<el-select v-model="ruleForm.xingbie" placeholder="" :disabled="false || ro.xingbie">
					<el-option v-for="(item, index) in xingbieOptions" :key="index" :label="item" :value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label="" prop="dianhua">
				<el-input v-model="ruleForm.dianhua" placeholder="" clearable
					:disabled="false || ro.dianhua"></el-input>
			</el-form-item>
			<el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
				v-if="type != 'cross' || (type == 'cross' && !ro.touxiang)" prop="touxiang">
				<file-upload tip="" action="file/upload" :limit="3" :multiple="true"
					:fileUrls="ruleForm.touxiang ? ruleForm.touxiang : ''" @change="touxiangUploadChange"></file-upload>
			</el-form-item>
			<el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' class="upload" v-else
				label="" prop="touxiang">
				<img v-if="ruleForm.touxiang.substring(0, 4) == 'http'" class="upload-img" style="margin-right:20px;"
					v-bind:key="index" :src="ruleForm.touxiang.split(',')[0]" width="100" height="100">
				<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index"
					v-for="(item, index) in ruleForm.touxiang.split(',')" :src="baseUrl + item" width="100" height="100">
			</el-form-item>

			<el-form-item :style='{ "padding": "0", "margin": "0" }'>
				<el-button
					:style='{ "border": "0", "cursor": "pointer", "padding": "0", "margin": "0 20px 0 0", "outline": "none", "color": "rgba(255, 255, 255, 1)", "borderRadius": "4px", "background": "#F5BB00", "width": "128px", "lineHeight": "40px", "fontSize": "14px", "height": "40px" }'
					type="primary" @click="onSubmit"></el-button>
				<el-button
					:style='{ "border": "0px solid rgba(64, 158, 255, 1)", "cursor": "pointer", "padding": "0", "margin": "0", "outline": "none", "color": "#fff", "borderRadius": "4px", "background": "#333", "width": "128px", "lineHeight": "40px", "fontSize": "14px", "height": "40px" }'
					@click="back()"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
export default {
	data() {
		let self = this
		return {
			id: '',
			baseUrl: '',
			ro: {
				paotuiyuanzhanghao: false,
				mima: false,
				paotuiyuanxingming: false,
				xingbie: false,
				dianhua: false,
				touxiang: false,
			},
			type: '',
			userTableName: localStorage.getItem('UserTableName'),
			ruleForm: {
				paotuiyuanzhanghao: '',
				mima: '',
				paotuiyuanxingming: '',
				xingbie: '',
				dianhua: '',
				touxiang: '',
			},
			xingbieOptions: [],


			rules: {
				paotuiyuanzhanghao: [
					{ required: true, message: '', trigger: 'blur' },
				],
				mima: [
					{ required: true, message: '', trigger: 'blur' },
				],
				paotuiyuanxingming: [
					{ required: true, message: '', trigger: 'blur' },
				],
				xingbie: [
				],
				dianhua: [
					{ validator: this.$validate.isMobile, trigger: 'blur' },
				],
				touxiang: [
				],
			},
			centerType: false,
		};
	},
	computed: {



	},
	components: {
	},
	created() {
		if (this.$route.query.centerType) {
			this.centerType = true
		}
		//this.bg();
		let type = this.$route.query.type ? this.$route.query.type : '';
		this.init(type);
		this.baseUrl = this.$config.baseUrl;
	},
	methods: {
		getMakeZero(s) {
			return s < 10 ? '0' + s : s;
		},
		// 
		download(file) {
			window.open(`${file}`)
		},
		// 
		init(type) {
			this.type = type;
			if (type == 'cross') {
				var obj = JSON.parse(localStorage.getItem('crossObj'));
				for (var o in obj) {
					if (o == 'paotuiyuanzhanghao') {
						this.ruleForm.paotuiyuanzhanghao = obj[o];
						this.ro.paotuiyuanzhanghao = true;
						continue;
					}
					if (o == 'mima') {
						this.ruleForm.mima = obj[o];
						this.ro.mima = true;
						continue;
					}
					if (o == 'paotuiyuanxingming') {
						this.ruleForm.paotuiyuanxingming = obj[o];
						this.ro.paotuiyuanxingming = true;
						continue;
					}
					if (o == 'xingbie') {
						this.ruleForm.xingbie = obj[o];
						this.ro.xingbie = true;
						continue;
					}
					if (o == 'dianhua') {
						this.ruleForm.dianhua = obj[o];
						this.ro.dianhua = true;
						continue;
					}
					if (o == 'touxiang') {
						this.ruleForm.touxiang = obj[o].split(",")[0];
						this.ro.touxiang = true;
						continue;
					}
				}
			} else if (type == 'edit') {
				this.info()
			}
			// 
			this.$http.get(this.userTableName + '/session', { emulateJSON: true }).then(res => {
				if (res.data.code == 0) {
					var json = res.data.data;
				}
			});
			this.xingbieOptions = ",".split(',')

			if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
				localStorage.removeItem('raffleType')
				setTimeout(() => {
					this.onSubmit()
				}, 300)
			}
		},

		// 
		// 
		info() {
			this.$http.get(`paotuiyuan/detail/${this.$route.query.id}`, { emulateJSON: true }).then(res => {
				if (res.data.code == 0) {
					this.ruleForm = res.data.data;
				}
			});
		},
		// 
		onSubmit() {

			//
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			this.$refs["ruleForm"].validate(valid => {
				if (valid) {
					if (this.type == 'cross') {
						var statusColumnName = localStorage.getItem('statusColumnName');
						var statusColumnValue = localStorage.getItem('statusColumnValue');
						if (statusColumnName && statusColumnName != '') {
							var obj = JSON.parse(localStorage.getItem('crossObj'));
							if (!statusColumnName.startsWith("[")) {
								for (var o in obj) {
									if (o == statusColumnName) {
										obj[o] = statusColumnValue;
									}
								}
								var table = localStorage.getItem('crossTable');
								this.$http.post(table + '/update', obj).then(res => { });
							} else {
								crossuserid = Number(localStorage.getItem('frontUserid'));
								crossrefid = obj['id'];
								crossoptnum = localStorage.getItem('statusColumnName');
								crossoptnum = crossoptnum.replace(/\[/, "").replace(/\]/, "");
							}
						}
					}
					if (crossrefid && crossuserid) {
						this.ruleForm.crossuserid = crossuserid;
						this.ruleForm.crossrefid = crossrefid;
						var params = {
							page: 1,
							limit: 10,
							crossuserid: crossuserid,
							crossrefid: crossrefid,
						}
						this.$http.get('paotuiyuan/list', {
							params: params
						}).then(res => {
							if (res.data.data.total >= crossoptnum) {
								this.$message({
									message: localStorage.getItem('tips'),
									type: 'error',
									duration: 1500,
								});
								return false;
							} else {
								// 


								this.$http.post(`paotuiyuan/${this.ruleForm.id ? 'update' : this.centerType ? 'save' : 'add'}`, this.ruleForm).then(res => {
									if (res.data.code == 0) {
										this.$message({
											message: '',
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.$router.go(-1);
											}
										});
									} else {
										this.$message({
											message: res.data.msg,
											type: 'error',
											duration: 1500
										});
									}
								});
							}
						});
					} else {


						this.$http.post(`paotuiyuan/${this.ruleForm.id ? 'update' : this.centerType ? 'save' : 'add'}`, this.ruleForm).then(res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				}
			});
		},
		// uuid
		getUUID() {
			return new Date().getTime();
		},
		// 
		back() {
			this.$router.go(-1);
		},
		touxiangUploadChange(fileUrls) {
			this.ruleForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl, "g"), "");
		},
	}
};
</script>

