<template>
  <div
    :style='{ "width": "100%", "padding": "30px 7% 40px", "margin": "0px auto", "position": "relative", "background": "#fff" }'>
    <el-form
      :style='{ "border": "1px solid #eee", "width": "100%", "padding": "30px", "position": "relative", "background": "#fcfcfc" }'
      class="add-update-preview" ref="ruleForm" :model="ruleForm" :rules="rules" label-width="180px">
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="paotuidanhao">
        <el-input v-model="ruleForm.paotuidanhao" placeholder="" clearable
          :disabled="false || ro.paotuidanhao"></el-input>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="paotuimingcheng">
        <el-input v-model="ruleForm.paotuimingcheng" placeholder="" clearable
          :disabled="false || ro.paotuimingcheng"></el-input>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="paotuileixing">
        <el-input v-model="ruleForm.paotuileixing" placeholder="" clearable
          :disabled="false || ro.paotuileixing"></el-input>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="paotuidizhi">
        <el-input v-model="ruleForm.paotuidizhi" placeholder="" clearable
          :disabled="false || ro.paotuidizhi"></el-input>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="shoujiandizhi">
        <el-input v-model="ruleForm.shoujiandizhi" placeholder="" clearable
          :disabled="false || ro.shoujiandizhi"></el-input>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="choujin">
        <el-input v-model.number="ruleForm.choujin" placeholder="" clearable
          :disabled="false || ro.choujin"></el-input>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        v-if="type != 'cross' || (type == 'cross' && !ro.paotuitupian)" prop="paotuitupian">
        <file-upload tip="" action="file/upload" :limit="3" :multiple="true"
          :fileUrls="ruleForm.paotuitupian ? ruleForm.paotuitupian : ''"
          @change="paotuitupianUploadChange"></file-upload>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' class="upload" v-else
        label="" prop="paotuitupian">
        <img v-if="ruleForm.paotuitupian.substring(0, 4) == 'http'" class="upload-img" style="margin-right:20px;"
          v-bind:key="index" :src="ruleForm.paotuitupian.split(',')[0]" width="100" height="100">
        <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index"
          v-for="(item, index) in ruleForm.paotuitupian.split(',')" :src="baseUrl + item" width="100" height="100">
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="songdashijian">
        <el-date-picker :disabled="true || ro.songdashijian" value-format="yyyy-MM-dd HH:mm:ss"
          v-model="ruleForm.songdashijian" type="datetime" placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="songdabeizhu">
        <el-input v-model="ruleForm.songdabeizhu" placeholder="" clearable
          :disabled="false || ro.songdabeizhu"></el-input>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="querenzhuangtai">
        <el-select v-model="ruleForm.querenzhuangtai" placeholder="" :disabled="true || ro.querenzhuangtai">
          <el-option v-for="(item, index) in querenzhuangtaiOptions" :key="index" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="yonghuzhanghao">
        <el-input v-model="ruleForm.yonghuzhanghao" placeholder="" clearable
          :disabled="false || ro.yonghuzhanghao"></el-input>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="yonghuxingming">
        <el-input v-model="ruleForm.yonghuxingming" placeholder="" clearable
          :disabled="false || ro.yonghuxingming"></el-input>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="paotuiyuanzhanghao">
        <el-input v-model="ruleForm.paotuiyuanzhanghao" placeholder="" clearable
          :disabled="false || ro.paotuiyuanzhanghao"></el-input>
      </el-form-item>
      <el-form-item :style='{ "padding": "10px", "margin": "0 0 10px", "background": "none" }' label=""
        prop="paotuiyuanxingming">
        <el-input v-model="ruleForm.paotuiyuanxingming" placeholder="" clearable
          :disabled="false || ro.paotuiyuanxingming"></el-input>
      </el-form-item>

      <el-form-item :style='{ "padding": "0", "margin": "0" }'>
        <el-button
          :style='{ "border": "0", "cursor": "pointer", "padding": "0", "margin": "0 20px 0 0", "outline": "none", "color": "rgba(255, 255, 255, 1)", "borderRadius": "4px", "background": "#F5BB00", "width": "128px", "lineHeight": "40px", "fontSize": "14px", "height": "40px" }'
          type="primary" @click="onSubmit"></el-button>
        <el-button
          :style='{ "border": "0px solid rgba(64, 158, 255, 1)", "cursor": "pointer", "padding": "0", "margin": "0", "outline": "none", "color": "#fff", "borderRadius": "4px", "background": "#333", "width": "128px", "lineHeight": "40px", "fontSize": "14px", "height": "40px" }'
          @click="back()"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    let self = this
    return {
      id: '',
      baseUrl: '',
      ro: {
        paotuidanhao: false,
        paotuimingcheng: false,
        paotuileixing: false,
        paotuidizhi: false,
        shoujiandizhi: false,
        choujin: false,
        paotuitupian: false,
        songdashijian: false,
        songdabeizhu: false,
        querenzhuangtai: false,
        yonghuzhanghao: false,
        yonghuxingming: false,
        paotuiyuanzhanghao: false,
        paotuiyuanxingming: false,
      },
      type: '',
      userTableName: localStorage.getItem('UserTableName'),
      ruleForm: {
        paotuidanhao: '',
        paotuimingcheng: '',
        paotuileixing: '',
        paotuidizhi: '',
        shoujiandizhi: '',
        choujin: '',
        paotuitupian: '',
        songdashijian: '',
        songdabeizhu: '',
        querenzhuangtai: '',
        yonghuzhanghao: '',
        yonghuxingming: '',
        paotuiyuanzhanghao: '',
        paotuiyuanxingming: '',
      },
      querenzhuangtaiOptions: [],


      rules: {
        paotuidanhao: [
        ],
        paotuimingcheng: [
        ],
        paotuileixing: [
        ],
        paotuidizhi: [
        ],
        shoujiandizhi: [
        ],
        choujin: [
          { validator: this.$validate.isIntNumer, trigger: 'blur' },
        ],
        paotuitupian: [
        ],
        songdashijian: [
        ],
        songdabeizhu: [
        ],
        querenzhuangtai: [
        ],
        yonghuzhanghao: [
        ],
        yonghuxingming: [
        ],
        paotuiyuanzhanghao: [
        ],
        paotuiyuanxingming: [
        ],
      },
      centerType: false,
    };
  },
  computed: {



  },
  components: {
  },
  created() {
    if (this.$route.query.centerType) {
      this.centerType = true
    }
    //this.bg();
    let type = this.$route.query.type ? this.$route.query.type : '';
    this.init(type);
    this.baseUrl = this.$config.baseUrl;
    this.ruleForm.songdashijian = this.getCurDateTime()
  },
  methods: {
    getMakeZero(s) {
      return s < 10 ? '0' + s : s;
    },
    // 
    download(file) {
      window.open(`${file}`)
    },
    // 
    init(type) {
      this.type = type;
      if (type == 'cross') {
        var obj = JSON.parse(localStorage.getItem('crossObj'));
        for (var o in obj) {
          if (o == 'paotuidanhao') {
            this.ruleForm.paotuidanhao = obj[o];
            this.ro.paotuidanhao = true;
            continue;
          }
          if (o == 'paotuimingcheng') {
            this.ruleForm.paotuimingcheng = obj[o];
            this.ro.paotuimingcheng = true;
            continue;
          }
          if (o == 'paotuileixing') {
            this.ruleForm.paotuileixing = obj[o];
            this.ro.paotuileixing = true;
            continue;
          }
          if (o == 'paotuidizhi') {
            this.ruleForm.paotuidizhi = obj[o];
            this.ro.paotuidizhi = true;
            continue;
          }
          if (o == 'shoujiandizhi') {
            this.ruleForm.shoujiandizhi = obj[o];
            this.ro.shoujiandizhi = true;
            continue;
          }
          if (o == 'choujin') {
            this.ruleForm.choujin = obj[o];
            this.ro.choujin = true;
            continue;
          }
          if (o == 'paotuitupian') {
            this.ruleForm.paotuitupian = obj[o].split(",")[0];
            this.ro.paotuitupian = true;
            continue;
          }
          if (o == 'songdashijian') {
            this.ruleForm.songdashijian = obj[o];
            this.ro.songdashijian = true;
            continue;
          }
          if (o == 'songdabeizhu') {
            this.ruleForm.songdabeizhu = obj[o];
            this.ro.songdabeizhu = true;
            continue;
          }
          if (o == 'querenzhuangtai') {
            this.ruleForm.querenzhuangtai = obj[o];
            this.ro.querenzhuangtai = true;
            continue;
          }
          if (o == 'yonghuzhanghao') {
            this.ruleForm.yonghuzhanghao = obj[o];
            this.ro.yonghuzhanghao = true;
            continue;
          }
          if (o == 'yonghuxingming') {
            this.ruleForm.yonghuxingming = obj[o];
            this.ro.yonghuxingming = true;
            continue;
          }
          if (o == 'paotuiyuanzhanghao') {
            this.ruleForm.paotuiyuanzhanghao = obj[o];
            this.ro.paotuiyuanzhanghao = true;
            continue;
          }
          if (o == 'paotuiyuanxingming') {
            this.ruleForm.paotuiyuanxingming = obj[o];
            this.ro.paotuiyuanxingming = true;
            continue;
          }
        }
      } else if (type == 'edit') {
        this.info()
      }
      // 
      this.$http.get(this.userTableName + '/session', { emulateJSON: true }).then(res => {
        if (res.data.code == 0) {
          var json = res.data.data;
          if ((json.paotuiyuanzhanghao != '' && json.paotuiyuanzhanghao) || json.paotuiyuanzhanghao == 0) {
            this.ruleForm.paotuiyuanzhanghao = json.paotuiyuanzhanghao
          }
          if ((json.paotuiyuanxingming != '' && json.paotuiyuanxingming) || json.paotuiyuanxingming == 0) {
            this.ruleForm.paotuiyuanxingming = json.paotuiyuanxingming
          }
        }
      });
      this.querenzhuangtaiOptions = ",".split(',')

      if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
        localStorage.removeItem('raffleType')
        setTimeout(() => {
          this.onSubmit()
        }, 300)
      }
    },

    // 
    // 
    info() {
      this.$http.get(`peisongjilu/detail/${this.$route.query.id}`, { emulateJSON: true }).then(res => {
        if (res.data.code == 0) {
          this.ruleForm = res.data.data;
        }
      });
    },
    // 
    onSubmit() {

      //
      var crossuserid;
      var crossrefid;
      var crossoptnum;
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          if (this.type == 'cross') {
            var statusColumnName = localStorage.getItem('statusColumnName');
            var statusColumnValue = localStorage.getItem('statusColumnValue');
            if (statusColumnName && statusColumnName != '') {
              var obj = JSON.parse(localStorage.getItem('crossObj'));
              if (!statusColumnName.startsWith("[")) {
                for (var o in obj) {
                  if (o == statusColumnName) {
                    obj[o] = statusColumnValue;
                  }
                }
                var table = localStorage.getItem('crossTable');
                this.$http.post(table + '/update', obj).then(res => { });
              } else {
                crossuserid = Number(localStorage.getItem('frontUserid'));
                crossrefid = obj['id'];
                crossoptnum = localStorage.getItem('statusColumnName');
                crossoptnum = crossoptnum.replace(/\[/, "").replace(/\]/, "");
              }
            }
          }
          if (crossrefid && crossuserid) {
            this.ruleForm.crossuserid = crossuserid;
            this.ruleForm.crossrefid = crossrefid;
            var params = {
              page: 1,
              limit: 10,
              crossuserid: crossuserid,
              crossrefid: crossrefid,
            }
            this.$http.get('peisongjilu/list', {
              params: params
            }).then(res => {
              if (res.data.data.total >= crossoptnum) {
                this.$message({
                  message: localStorage.getItem('tips'),
                  type: 'error',
                  duration: 1500,
                });
                return false;
              } else {
                // 


                this.$http.post(`peisongjilu/${this.ruleForm.id ? 'update' : this.centerType ? 'save' : 'add'}`, this.ruleForm).then(res => {
                  if (res.data.code == 0) {
                    this.$message({
                      message: '',
                      type: 'success',
                      duration: 1500,
                      onClose: () => {
                        this.$router.go(-1);
                      }
                    });
                  } else {
                    this.$message({
                      message: res.data.msg,
                      type: 'error',
                      duration: 1500
                    });
                  }
                });
              }
            });
          } else {


            this.$http.post(`peisongjilu/${this.ruleForm.id ? 'update' : this.centerType ? 'save' : 'add'}`, this.ruleForm).then(res => {
              if (res.data.code == 0) {
                this.$message({
                  message: '',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.$router.go(-1);
                  }
                });
              } else {
                this.$message({
                  message: res.data.msg,
                  type: 'error',
                  duration: 1500
                });
              }
            });
          }
        }
      });
    },
    // uuid
    getUUID() {
      return new Date().getTime();
    },
    // 
    back() {
      this.$router.go(-1);
    },
    paotuitupianUploadChange(fileUrls) {
      this.ruleForm.paotuitupian = fileUrls.replace(new RegExp(this.$config.baseUrl, "g"), "");
    },
  }
};
</script>

